import React, { useState, useEffect } from "react";
import axios from "axios";
import slika from "./img/aa.webp";
import { host } from "./utils/utils";
import "./App.css";

function App() {
  const [localName, setLocalName] = useState("");
  const [name, setName] = useState("");
  const [fingerprint, setFingerprint] = useState({});

  useEffect(() => {
    setLocalName(localStorage.getItem("name"));

    const newFingerprint = {
      ram: navigator.deviceMemory,
      cores: navigator.hardwareConcurrency,
      platform: navigator.platform,
      userAgent: navigator.userAgent,
      gpu: getGPU(),
    };

    console.log(newFingerprint);
    setFingerprint(newFingerprint);
  }, []);

  const getGPU = () => {
    const gl = document.createElement("canvas").getContext("webgl");

    if (!gl) {
      return "no webgl";
    }

    const debugInfo = gl.getExtension("WEBGL_debug_renderer_info");

    if (debugInfo) {
      return gl.getParameter(debugInfo.UNMASKED_RENDERER_WEBGL);
    }

    return "no WEBGL_debug_renderer_info";
  };

  const sendData = async () => {
    if (name != "") {
      const res = await axios.post(`${host()}/fingerprint`, {
        name,
        fingerprint,
      });

      console.log(res.data);

      localStorage.setItem("name", name);
      setLocalName(name);
    } else {
      alert("unesi ime pls");
    }
  };

  return (
    <div className="App">
      <div className="container">
        <img src={slika} alt="slika" />

        {!localName ? (
          <>
            <h3>Unesi ime i prezime i pomozi u testiranju!</h3>
            <br />
            <input
              type="text"
              value={name}
              onChange={(e) => setName(e.target.value)}
              placeholder="ali stvarno ime i prezime"
            />
            <br />
            <br />
            <button onClick={() => sendData()}> To je to! </button>
          </>
        ) : (
          <>
            <h2>Cao {localName}</h2>
            <h3>Hvala na pomoci!</h3>
          </>
        )}
      </div>
    </div>
  );
}

export default App;
